.popup__container {
  position: relative;
  box-sizing: border-box;
  width: 430px;
  border-radius: 10px;
  background: #ffffff;
  color: black;
  margin: 0 auto;
  padding: 34px 36px 37px;
  box-shadow: 0px 0px 25px rgba(0,0,0,0.15);
  text-align: center;
}
@media screen and (max-width: 600px) {
  .popup__container {
    width: 282px;
    padding: 25px 22px;
  }
}
